import { UserResource } from "@clerk/types"
import { Watchlist } from "@rp/common/api-types"
import { defaultBigdataAvatar, defaultRavenPackAvatar } from "@rp/react/common/assets"

export const getWatchlistOwnerInfo = (
  watchlist: Watchlist | undefined,
  user?: UserResource | null | undefined,
) => {
  const userIsOwner = user?.id === watchlist?.ownerUserId
  if (userIsOwner) {
    return {
      displayName: "You",
      name: "You",
      avatar: watchlist?.ownerImageUrl ?? defaultRavenPackAvatar,
    }
  }

  const isThematic = watchlist?.global?.type === "thematic"
  if (isThematic) {
    return {
      displayName: watchlist.global?.author?.name ?? "RavenPack",
      name: watchlist.global?.author?.name ?? "RavenPack",
      avatar: watchlist?.global?.author?.avatarUrl ?? defaultRavenPackAvatar,
    }
  }

  const isCommunityWatchlist =
    watchlist?.global?.type === "community" && watchlist?.global?.status === "published"
  if (isCommunityWatchlist) {
    return {
      displayName: watchlist.global?.author?.name ?? "bigdata",
      name: watchlist.global?.author?.name ?? "bigdata",
      avatar: watchlist?.global?.author?.avatarUrl ?? defaultBigdataAvatar,
    }
  }

  return {
    displayName: watchlist?.ownerName ?? "Anonymous User",
    name: watchlist?.ownerName ?? "Anonymous User",
    avatar: watchlist?.ownerImageUrl ?? "",
  }
}
